@import url("http://cdn.syncfusion.com/ej2/material.css");

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.page-containeer{
 display: flex;
 flex-direction: column;
 min-height: 100vh;
}

.content-wrap{
  flex:1;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.some-page-wrapper {
  margin: 15px;
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.column {
  display: table-cell;
  width: 50%;
  padding: 20px;
  margin: 10px;
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1;
}

.double-column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 2;
}

.pic-size{
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
  max-width: 350px;
}
 
h1{
  font-size: 2.8vw;
font-family: tbudrgothic-std, sans-serif;
font-weight: 900;
font-style: normal;
color: #dd3b3b;
margin-bottom: 20px;
}

p{
  font-size: 18px;
line-height: 2;
padding-bottom: 30px;
} 

button-text{
  font-size: 20px;
  font-family: 'Poppins', cursive;
}

body  {
  background-image: url("../../pictures/background.jpg");
  background-color: #cccccc;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
